import React, { Component } from 'react';
import './App.css';
import logo from './IB-ProjectsLogo.jpg';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav'
import {BrowserRouter as Router,Link, Route} from 'react-router-dom';
import Projects from '../components/Projects/Projects';
import Work from '../components/Work/Work';
import AboutMe from '../components/AboutMe/AboutMe';
import Home from '../components/Home/Home';
import 'tachyons';
class App extends Component {
  render(){
    return(
        <Router>
        <Navbar collapseOnSelect expand="lg" bg="white" variant="light">
        <img src={logo} className="logo" alt="logo "/>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-md-end">
            <Nav className="tc">
            <Nav.Link><Link to="/" className="themecolor1 link">Home</Link></Nav.Link>
            <Nav.Link><Link to="/projects" className="themecolor1 link">Projects</Link></Nav.Link>
            <Nav.Link><Link to="/work" className="themecolor1 link">Work</Link></Nav.Link>
            <Nav.Link><Link to="/aboutme" className="themecolor1 link">About Me</Link></Nav.Link>
            </Nav>
        </Navbar.Collapse>
        </Navbar>
        <Route exact path="/" component ={Home}/>
        <Route path="/projects" component={Projects}/>
        <Route path="/work" component={Work}/>
        <Route path="/aboutme" component={AboutMe}/>
       </Router>
        
    )}
}

export default App;
